import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import '../styles/LoginForm.css'; // Import your CSS file for styling

const LoginForm = () => {
    const [email, setEmail] = useState('');
    const [unicode, setUniCode] = useState('');
    const [error, setError] = useState('');
    
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        try {
            // Make API call to validate credentials
            const response = await fetch('https://hook.eu2.make.com/6804y1poalszf8gt454o1ekk9sf87c3n ', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ email, unicode }),
            });
            
            const data = await response.status;
            console.log(data)

            if (response && data === 205) {
                navigate('/call-section');
            } else {
                setError('Incorrect email or uniCode');
            }
        } catch (error) {
            setError('Error validating credentials');
        }
    };

    return (
        <form className="login-form" onSubmit={handleSubmit}>
            <center><h1>Login</h1></center>
            <TextField
                label="Email"
                variant="outlined"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                fullWidth
                margin="normal"
            />
            <TextField
                label="UniCode"
                variant="outlined"
                type="password"
                value={unicode}
                onChange={(e) => setUniCode(e.target.value)}
                fullWidth
                margin="normal"
            />
            <Button type="submit" variant="contained" color="primary" fullWidth>
                Login
            </Button>
            {error && <p className="error-message">{error}</p>}
        </form>
    );
};

export default LoginForm;
